@tailwind base;
/* Let's get this party started */
::-webkit-scrollbar {
  @apply w-2;
}
/* Track */
::-webkit-scrollbar-track {
  @apply bg-line-dimmed/0;
}
/* Handle */
::-webkit-scrollbar-thumb {
  @apply bg-line rounded-xl;
}
::-webkit-scrollbar-thumb:hover {
    @apply bg-content-dimmed;
  }
::-webkit-scrollbar-thumb:window-inactive {
  @apply bg-line/50;
}
hr {
  @apply border-line-dimmed;
}
label {
  @apply inline-block text-sm font-semibold cursor-pointer;
}
/* Headings */
h1 {
  @apply text-2xl font-bold;
}
h2 {
  @apply text-lg font-bold;
}
h3 {
  @apply text-base font-bold;
}
@tailwind components;
.tippy-content {
  @apply text-xs;
}
.ti {
  @apply text-2xl;
}
.box {
  @apply p-4 border border-line-dimmed rounded-xl;
}
.box[data-density='comfortable'] {
    @apply px-8 py-6 sm:px-16 sm:py-12;
  }
.box.box-selected {
    @apply outline outline-1 outline-offset-4 outline-line-dimmed border-line hover:outline-line;
  }
.loading {
  @apply rounded-lg bg-line-dimmed animate-pulse;
}
.avatar {
  @apply relative w-10 h-10 rounded-full bg-intermediate text-intermediate-content flex items-center justify-center font-bold shrink-0 text-xs uppercase whitespace-nowrap;
}
.avatar > img {
    @apply absolute top-0 left-0 object-cover w-full h-full rounded-full;
  }
.avatar > svg {
    @apply w-6 h-6;
  }
a.full {
  @apply absolute top-0 left-0 w-full h-full;
}
.gallery li:nth-of-type(15n + 1) {
    @apply col-span-2 row-span-2;
  }
.button {
  @apply inline-flex 
        items-center
        px-6 
        h-12 
        rounded-lg 
        text-accent-content 
        bg-accent 
        text-center 
        font-medium 

        justify-center 
        gap-2
        transition-colors;
}
.button:not(.button-outline) {
    @apply hover:ring-offset-2
        hover:ring-accent
        hover:ring-1
        hover:ring-offset-main;
  }
.bg-intermediate .button:not(.button-outline) {
      @apply hover:ring-offset-intermediate;
    }
.button:disabled {
    @apply opacity-50 
            cursor-not-allowed 
            pointer-events-none
            bg-line-dimmed;
  }
.button[processing] {
    @apply opacity-50
            cursor-not-allowed 
            pointer-events-none;
  }
.button .spinner {
    @apply hidden;
  }
.button[processing] .spinner {
    @apply block;
  }
.button-secondary {
  @apply bg-button text-button-content;
}
.button-secondary:not(.button-outline) {
    @apply hover:ring-button;
  }
.bg-intermediate .button-secondary:not(.button-outline) {
      @apply hover:ring-offset-intermediate;
    }
.button-intermediate {
  @apply bg-intermediate text-intermediate-content;
}
.button-intermediate:not(.button-outline) {
    @apply hover:ring-intermediate;
  }
.button-failure {
  @apply bg-failure;
}
.button-failure:not(.button-outline) {
    @apply hover:ring-failure;
  }
.button-sm {
  @apply h-10 px-4;
}
.button-xs {
  @apply h-7 px-2 py-0.5 text-sm rounded-md gap-1;
}
.button-outline {
  @apply bg-transparent 
        border 
        border-line-dimmed 
        text-content
        hover:border-line;
}
.input {
  @apply bg-transparent block w-full rounded-lg border border-line h-12 p-3 appearance-none font-normal text-base;
}
.input:disabled {
    @apply bg-line-dimmed cursor-not-allowed opacity-50;
  }
textarea.input {
  @apply min-h-[48px] resize-y h-auto;
}
input[type='radio'].radio-button {
  @apply hidden;
}
input[type='radio'].radio-button + span {
    @apply inline-flex px-3 py-1 border rounded-lg cursor-pointer border-line hover:border-content;
  }
input[type='radio'].radio-button:checked + span {
    @apply bg-button border-button text-button-content cursor-default;
  }
input[type='radio'].radio-button:disabled + span {
    @apply border-line-dimmed cursor-not-allowed opacity-75;
  }
label:has(+ [required]) {
  @apply before:text-failure before:content-['*'] before:text-xs;
}
label .help {
  @apply text-base text-content-dimmed transition-all hover:scale-125 hover:text-intermediate-content;
}
.autogrow-textarea {
  @apply grid after:whitespace-pre-wrap after:invisible after:row-start-1 after:col-start-1 after:row-end-2 after:col-end-2;
}
.autogrow-textarea:after {
    content: attr(data-replicated-value) ' ';
  }
.autogrow-textarea > textarea {
    @apply resize-none overflow-hidden row-start-1 col-start-1 row-end-2 col-end-2;
    font: inherit;
  }
.badge {
  @apply px-2 py-0.5 text-sm bg-intermediate text-intermediate-content rounded-md;
}
.badge-success {
  @apply bg-success/10 text-success;
}
@keyframes enter {
  from {
    @apply drop-shadow grayscale opacity-0 bg-transparent;
    transform: scale(1.05) translateX(var(--x, 0px));
  }
}
@keyframes enter-content {
  from {
    transform: translateY(10px);
    opacity: 0.2;
  }
}
.preset-card {
  --view: 0px;

  animation: enter both linear;
  animation-timeline: view(36px);
  animation-range: entry;
}
.preset-card h3,
  .preset-card p,
  .preset-card .avatar {
    animation: enter-content both linear;
    animation-timeline: view(var(--view)));
    animation-range: entry;
  }
.preset-card:nth-child(3n + 1 of .preset-card) {
    --x: -36px;
    transform-origin: left center;
  }
.preset-card:nth-child(3n + 1 of .preset-card) .avatar {
      --view: 200px;
    }
.preset-card:nth-child(3n + 1 of .preset-card) h3,
    .preset-card:nth-child(3n + 1 of .preset-card) p {
      --view: 3000px;
    }
.preset-card:nth-child(3n + 2 of .preset-card) {
    --x: 0%;
  }
.preset-card:nth-child(3n + 2 of .preset-card) .avatar {
      --view: 50px;
    }
.preset-card:nth-child(3n + 2 of .preset-card) h3,
    .preset-card:nth-child(3n + 2 of .preset-card) p {
      --view: 100px;
    }
.preset-card:nth-child(3n + 3 of .preset-card) {
    --x: 36px;
    transform-origin: right center;

  }
.preset-card:nth-child(3n + 3 of .preset-card) .avatar {
      --view: 200px;
    }
.preset-card:nth-child(3n + 3 of .preset-card) h3,
    .preset-card:nth-child(3n + 3 of .preset-card) p {
      --view: 3000px;
    }
.hljs {
  color: #383a42;
  background: #fafafa;
}
.hljs::-moz-selection, .hljs ::-moz-selection {
  background-color: #e5e5e6;
  color: #383a42;
}
.hljs::selection,
.hljs ::selection {
  background-color: #e5e5e6;
  color: #383a42;
}
/* purposely do not highlight these things */
.hljs-formula,
.hljs-params,
.hljs-property {
}
/* base03 - #a0a1a7 -  Comments, Invisibles, Line Highlighting */
.hljs-comment {
  color: #a0a1a7;
}
/* base04 - #696c77 -  Dark Foreground (Used for status bars) */
.hljs-tag {
  color: #696c77;
}
/* base05 - #383a42 -  Default Foreground, Caret, Delimiters, Operators */
.hljs-subst,
.hljs-punctuation,
.hljs-operator {
  color: #383a42;
}
.hljs-operator {
  opacity: 0.7;
}
/* base08 - Variables, XML Tags, Markup Link Text, Markup Lists, Diff Deleted */
.hljs-bullet,
.hljs-variable,
.hljs-template-variable,
.hljs-selector-tag,
.hljs-name,
.hljs-deletion {
  color: #ca1243;
}
/* base09 - Integers, Boolean, Constants, XML Attributes, Markup Link Url */
.hljs-symbol,
.hljs-number,
.hljs-link,
.hljs-attr,
.hljs-variable.constant_,
.hljs-literal {
  color: #d75f00;
}
/* base0A - Classes, Markup Bold, Search Text Background */
.hljs-title,
.hljs-class .hljs-title,
.hljs-title.class_ {
  color: #c18401;
}
.hljs-strong {
  font-weight: bold;
  color: #c18401;
}
/* base0B - Strings, Inherited Class, Markup Code, Diff Inserted */
.hljs-code,
.hljs-addition,
.hljs-title.class_.inherited__,
.hljs-string {
  color: #50a14f;
}
/* base0C - Support, Regular Expressions, Escape Characters, Markup Quotes */
.hljs-built_in,
.hljs-doctag, /* guessing */
.hljs-quote,
.hljs-keyword.hljs-atrule,
.hljs-regexp {
  color: #0184bc;
}
/* base0D - Functions, Methods, Attribute IDs, Headings */
.hljs-function .hljs-title,
.hljs-attribute,
.ruby .hljs-property,
.hljs-title.function_,
.hljs-section {
  color: #4078f2;
}
/* base0E - Keywords, Storage, Selector, Markup Italic, Diff Changed */
.hljs-type,
.hljs-template-tag,
.diff .hljs-meta,
.hljs-keyword {
  color: #a626a4;
}
.hljs-emphasis {
  color: #a626a4;
  font-style: italic;
}
.hljs-meta,
/*
  prevent top level .keyword and .string scopes
  from leaking into meta by accident
*/
.hljs-meta .hljs-keyword,
.hljs-meta .hljs-string {
  color: #986801;
}
.hljs-meta .hljs-keyword,
/* for v10 compatible themes */
.hljs-meta-keyword {
  font-weight: bold;
}
