.editor {
  @apply prose 
    max-w-none 
    font-editor 
    prose-headings:font-editor-heading
    prose-headings:font-semibold
    prose-h1:text-2xl
    prose-h2:text-xl
    prose-h3:text-lg
    prose-h4:text-base
    prose-pre:p-6
    prose-pre:relative
    prose-pre:shadow-sm
    prose-pre:rounded-lg
    prose-pre:bg-line-dimmed/25;
}
.editor > *:first-child {
    @apply mt-0;
  }
.editor > *:last-child {
    @apply mb-0;
  }
.editor pre {
    @apply whitespace-pre-wrap;
  }
.editor pre svg {
    @apply absolute top-0 left-0 w-full h-full rounded-lg text-line-dimmed group-hover:text-line group-data-[selected]:hidden -z-10;
  }
.editor pre .lang {
    @apply absolute right-6 top-6 text-xs text-content-dimmed uppercase pointer-events-none;
  }
/* Dropdown context menu */
.context-menu {
  @apply absolute 
    z-20 
    
    right-0 
    mt-1 
    top-full
    
    hidden
    border 
    rounded-lg 
    
    border-line-dimmed 
    bg-main 
    
    data-[open]:block  
    
    whitespace-nowrap
    
    max-h-max
    overflow-auto;

  min-width: 8rem;

  box-shadow: 0px 4px 6px -2px rgba(0, 0, 9, 0.03),
    0px 12px 16px -4px rgba(0, 0, 9, 0.08);
}
.context-menu.context-menu-bl {
    @apply left-0 right-auto;
  }
body {
