.topcontrol {
        background: #86c156 none repeat scroll 0 0;
        border-radius: 30px;
        bottom: 5px;
        box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
        color: #232434;
        cursor: pointer;
        font-size: 22px;
        height: 50px;
        line-height: 47px;
        opacity: 1;
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
        position: fixed;
        right: 5px;
        text-align: center;
        transition: all 0.2s ease 0s;
        width: 50px;
    }
