/*START PRELOADER DESIGN*/
.preloader {
        background: #0e1123;
        bottom: 0;
        left: 0;
        position: fixed;
        right: 0;
        top: 0;
        z-index: 99999;
    }
.status-mes {
        width: 50px;
        height: 50px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin: -40px 0 0 -40px;
        font-size: 10px;
        text-indent: -12345px;
        border-top: 48px solid #86c156;;
        border-right: 48px solid #86c156;;
        border-bottom: 48px solid #86c156;;
        border-left: 48px solid #151a32;
        border-radius: 50%;
        animation: spinner 700ms infinite linear;
        z-index: 10000;
    }
